import CryptoJS from "crypto-js";

const SECRET = process.env.NEXT_PUBLIC_ENCRYPTION_SECRET!;

export function encrypt(text: string) {
  return CryptoJS.AES.encrypt(text, SECRET).toString();
}

export function decrypt(cipher: string) {
  return CryptoJS.AES.decrypt(cipher, SECRET).toString(CryptoJS.enc.Utf8);
}

// ÿØÿßŸÑÿ© ŸÖÿ≥ÿßÿπÿØÿ© ŸÑŸÖÿπÿ±ŸÅÿ© ÿ•ÿ∞ÿß ŸÉÿßŸÜ ÿßŸÑÿπÿ±ÿ∂ ÿµÿ∫Ÿäÿ± (ÿ¨ŸàÿßŸÑ)
export function isMobile() {
  if (typeof window === 'undefined') return false;
  return window.innerWidth <= 768; // ÿ≤ŸäÿßÿØÿ© ÿßŸÑÿ≠ÿØ ŸÑŸäÿ¥ŸÖŸÑ ÿßŸÑŸÖÿ≤ŸäÿØ ŸÖŸÜ ÿßŸÑÿ£ÿ¨Ÿáÿ≤ÿ©
}

// ÿØÿßŸÑÿ© ÿ£ŸÉÿ´ÿ± ÿØŸÇÿ© ŸÑŸÖÿπÿ±ŸÅÿ© ÿ≠ÿ¨ŸÖ ÿßŸÑÿ¥ÿßÿ¥ÿ©
export function getScreenSize() {
  if (typeof window === 'undefined') return 'desktop';
  const width = window.innerWidth;
  if (width <= 480) return 'mobile';
  if (width <= 768) return 'tablet';
  if (width <= 1024) return 'small-desktop';
  return 'desktop';
}

// ÿØÿßŸÑÿ© ŸÑŸÑÿ™ÿ≠ŸÇŸÇ ŸÖŸÜ ÿ£ŸÜ ÿßŸÑÿ¥ÿßÿ¥ÿ© ÿµÿ∫Ÿäÿ±ÿ© ÿ¨ÿØÿßŸã
export function isSmallScreen() {
  if (typeof window === 'undefined') return false;
  return window.innerWidth <= 480;
} 